<template>
  <MyTemplate>
    <template v-slot:myContent>
      <b-row>
        <!-- progressbar -->
        <div class="col-md-12">
          <div class="progressbar-container text-center">
            <ul class="progressbar">
              <li class="progressbar-done">檢視購物車</li>
              <li class="progressbar-active">付款方式及寄送資訊</li>
              <li>確認訂單資料</li>
              <li>訂單成立</li>
            </ul>
          </div>
        </div>
        <!-- progressbar -->
      </b-row>

      <b-form @submit="onSubmit">
        <b-row class="mt-3">
          <b-col md="12">
            <b-card
              header="配送方式"
              header-text-variant="dark"
              header-tag="header"
              header-bg-variant="muted"
            >
              <b-row>
                <span class="mr-3"></span>
                <b-form-radio
                  :inline="true"
                  v-model="deliverForm.deliver"
                  name="delivery"
                  value="homeDelivery"
                  >宅配</b-form-radio
                >
                <b-form-radio
                  :inline="true"
                  v-model="deliverForm.deliver"
                  name="delivery"
                  value="postalDelivery"
                  >郵寄</b-form-radio
                >
              </b-row>
            </b-card>
          </b-col>
        </b-row>

        <b-row class="mt-3">
          <b-col md="12">
            <b-card
              header="付款方式"
              header-text-variant="dark"
              header-tag="header"
              header-bg-variant="muted"
            >
              <b-row>
                <span class="mr-3"></span>
                <b-form-radio
                  :inline="true"
                  v-model="deliverForm.payment"
                  name="payment"
                  value="COD"
                  >貨到付款</b-form-radio
                >
              </b-row>
            </b-card>
          </b-col>
        </b-row>

        <b-row class="mt-3">
          <b-col md="12">
            <b-card
              header="發票資訊"
              header-text-variant="dark"
              header-tag="header"
              header-bg-variant="muted"
            >
              <b-row>
                <span class="mr-3"></span>
                <b-form-radio
                  :inline="true"
                  v-model="deliverForm.invoiceType"
                  name="invoiceType"
                  value="paper"
                  >紙本發票</b-form-radio
                >
              </b-row>
            </b-card>
          </b-col>
        </b-row>

        <b-row class="mt-3">
          <b-col md="12">
            <b-card
              header="訂購人資訊"
              header-text-variant="dark"
              header-tag="header"
              header-bg-variant="muted"
            >
              <b-row class>
                <b-col md="2" class="d-flex align-items-center ml-1">
                  <label>訂購人</label>
                </b-col>
                <b-col md="9" class="ml-1">
                  <span>訂購人姓名</span>
                </b-col>
              </b-row>

              <b-row class>
                <b-col md="2" class="d-flex align-items-center ml-1">
                  <label>聯絡電話</label>
                </b-col>
                <b-col md="9" class="ml-1">
                  <span>訂購人聯絡電話</span>
                </b-col>
              </b-row>

              <b-row class>
                <b-col md="2" class="d-flex align-items-center ml-1">
                  <label for="buyer-address">地址</label>
                </b-col>
                <b-col md="9" class="ml-1">
                  <span>訂購人地址</span>
                </b-col>
              </b-row>
            </b-card>
          </b-col>
        </b-row>

        <b-row class="mt-3">
          <b-col md="12">
            <b-card header-tag="header">
              <template v-slot:header>
                <div class="bg-muted text-dark">
                  <span class="mr-3">收件人資訊</span>
                  <b-form-radio
                    :inline="true"
                    v-model="deliverForm.selected"
                    name="radioBtn"
                    value="same"
                    >同訂購人</b-form-radio
                  >
                  <b-form-radio
                    :inline="true"
                    v-model="deliverForm.selected"
                    name="radioBtn"
                    value="new"
                    >修改收件人資訊</b-form-radio
                  >
                </div>
              </template>

              <b-row class v-show="show">
                <b-col md="2" class="d-flex align-items-center ml-1">
                  <label for="receiver-name">收件人</label>
                </b-col>
                <b-col md="9">
                  <b-form-input
                    size="lg"
                    v-model="deliverForm.receiverName"
                    id="receiver-name"
                    name="receiver-name"
                    placeholder="請輸入收件人的姓名"
                  ></b-form-input>
                </b-col>
              </b-row>

              <b-row class="mt-3" v-show="show">
                <b-col md="2" class="d-flex align-items-center ml-1">
                  <label for="receiver-phone">聯絡電話</label>
                </b-col>
                <b-col md="9">
                  <b-form-input
                    size="lg"
                    v-model="deliverForm.receiverPhone"
                    id="receiver-phone"
                    name="receiver-phone"
                    placeholder="請輸入收件人的聯絡電話"
                  ></b-form-input>
                </b-col>
              </b-row>

              <b-row class="mt-3" v-show="show">
                <b-col md="2" class="d-flex align-items-start ml-1">
                  <label for="receiver-address">地址</label>
                </b-col>
                <b-col md="9">
                  <twzipcode
                    :class-names="{
                      county: 'form-control form-control-lg',
                      district: 'form-control form-control-lg',
                      zipcode: 'form-control form-control-lg',
                    }"
                    default-zipcode="100"
                    ref="twzipcode"
                    @update="result"
                  ></twzipcode>
                  <b-form-input
                    size="lg"
                    v-model="deliverForm.receiverStreet"
                    class="mt-1"
                    id="receiver-address"
                    name="receiver-address"
                    placeholder="請輸入收件人地址"
                  ></b-form-input>
                </b-col>
              </b-row>
            </b-card>

            <div class="d-flex justify-content-end mt-3">
              <b-button class="mr-3" variant="outline-primary">
                <font-awesome-icon
                  :icon="['fas', 'angle-double-left']"
                />&nbsp;上一步
              </b-button>
              <b-button type="submit" class="mr-3" variant="outline-primary">
                下一步&nbsp;
                <font-awesome-icon :icon="['fas', 'angle-double-right']" />
              </b-button>
            </div>
          </b-col>
        </b-row>
      </b-form>
    </template>
  </MyTemplate>
</template>
 
<script>
import MyTemplate from "@/components/MyTemplate.vue";
import styles from "@/assets/css/progress-bar.css";
import Twzipcode from "vue2-twzipcode";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faAngleDoubleRight,
  faAngleDoubleLeft,
} from "@fortawesome/free-solid-svg-icons";

library.add(faAngleDoubleRight, faAngleDoubleLeft);

export default {
  name: "Deliver",
  data() {
    return {
      deliverForm: {
        deliver: "postalDelivery",
        payment: "",
        invoiceType: "paper",
        selected: "same",
        zipcode: "",
        county: "",
        district: "",
        receiverName: "",
        receiverPhone: "",
        receiverStreet: "",
      },
    };
  },
  components: {
    MyTemplate,
    Twzipcode,
  },
  // https://vuejs.org/v2/guide/computed.html#Computed-Caching-vs-Methods
  // https://cn.vuejs.org/v2/guide/computed.html#%E8%AE%A1%E7%AE%97%E5%B1%9E%E6%80%A7-vs-Methods
  computed: {
    show: function () {
      return this.deliverForm.selected === "new";
    },
  },
  methods: {
    result({ zipcode, county, district }) {
      this.deliverForm.zipcode = zipcode;
      this.deliverForm.county = county;
      this.deliverForm.district = district;
      console.log(this.deliverForm.zipcode);
      console.log(this.deliverForm.county);
      console.log(this.deliverForm.district);
    },
    onSubmit(event) {
      event.preventDefault();
      //alert(JSON.stringify(this.deliverForm));
      console.log(JSON.stringify(this.deliverForm));
    },
  },
  mounted() {},
};
</script>